import {ProfileAPI} from "../API/ProfileAPI";

const ADD_POST = 'ADD-POST';
const SET_USERS_PROFILE = 'SET_USERS_PROFILE';
const SET_STATUS = 'SET_STATUS';
const DELETE_POST = 'DELETE_POST';

let initialState = {
    posts: [
        {
            id: 12,
            message: 'Hey, how are you?',
            likesCount: 24,

        },
        {
            id: 11,
            message: 'Its my first post',
            likesCount: 2,

        }],
    profile: '',
    status: '',
}

const profileReducer = (state = initialState, action) => {

    switch (action.type) {
        case ADD_POST:
            let text = action.newPost
            return {
                ...state,
                posts: [{
                    id: 13,
                    message: text,
                    likesCount: 0,
                    img: 'https://coolsen.ru/wp-content/uploads/2021/09/55.jpg'
                }, ...state.posts]
            };
        case SET_USERS_PROFILE: {
            return {
                ...state, profile: action.profile
            }
        }
        case SET_STATUS: {
            return {
                ...state, status: action.status
            }
        }
        case DELETE_POST: {
            return {
                ...state, posts: state.posts.filter(p => p.id !== action.postId)
            }
        }
        default:
            return state;
    }
}

export const addPost = (newPost) => ({type: ADD_POST, newPost})
export const setUsersProfile = (profile) => ({type: SET_USERS_PROFILE, profile})
export const setStatus = (status) => ({type: SET_STATUS, status})
export const deletePost = (postId) => ({type: DELETE_POST, postId})
export const getUsersProfile = (userId) => async (dispatch) => {
    let response = await ProfileAPI.Profile(userId)
    dispatch(setUsersProfile(response.data));
}

export const getUsersStatus = (userId) => async (dispatch) => {
    let response = await ProfileAPI.ProfileStatus(userId)
    dispatch(setStatus(response.data))
}

export const updateStatus = (status) => async (dispatch) => {
    let response = await ProfileAPI.UpdateStatus(status)
    if (response.data.resultCode === 0) {
        dispatch(setStatus(status))
    }
}

export default profileReducer;